body {
  font-family: sans-serif;
}

input {
  font-size: 24px;
  padding: 0.5em;
  border-radius: 20px;
  border: none;
  width: 70%;
  margin-right: 5%;
}

button {
  font-size: 24px;
  padding: 0.3em;
  border-radius: 20px;
  border: none;
  width: 20%;
}

li {
  font-size: 24px;
}

h1,
h2,
h3 {
  text-align: center;
}

h3 {
  color: blue;
}

section {
  padding: 10px;
  border-radius: 5px;
  margin: 10px;
}

.input {
  background-color: lightskyblue;
}

.yet {
  background-color: lightblue;
}

.done {
  background-color: lightgoldenrodyellow;
}

.list {
  display: flex;
  justify-content: space-between;
  height: 50px;
  align-items: center;
  margin: 5px 0;
}

.list p {
  width: 55%;
}
